import { z } from "zod"

export const ZStatusCell = z.object({
  enumKey: z.string(),
})

export type TStatusCell = z.infer<typeof ZStatusCell>

export const ZStatusColumnOptions = z.array(
  z.object({
    label: z.string(),
    backgroundColor: z.string(),
    enumKey: z.string(),
  })
)

export type TStatusColumnOptions = z.infer<typeof ZStatusColumnOptions>
